/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  margin: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 2rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.citas {
  list-style: none;
  padding: 0;
}
.citas li {
  padding: 1 rem;
  border-top: px solid #FFFFFF;
}
.citas li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span,
.citas li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}